/* color variables */
/* border radius */
*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body {
    font-family: Mukta, sans-serif;
    height: 100vh;
    display: grid;
    justify-content: center;
    align-items: center;
    color: #4f546c;
    font-size: 0.9rem;
    background-color: #f9fbff;
}
.tablee {
    min-width: 78vw;
    border-collapse: collapse;
    box-shadow: 0 5px 10px #e1e5ee;
    background-color: white;
    text-align: left;
    overflow: hidden;
    border-radius: 10px;
}
.tablee thead {
    box-shadow: 0 5px 10px #e1e5ee;
}
.tablee th {
    padding: 1rem 2rem;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
    font-size: 0.7rem;
    font-weight: 900;
}
.tablee td {
    padding: 1rem 2rem;
}

.tablee .amount {
    text-align: right;
}
.tablee tr:nth-child(even) {
    background-color: #FFEFF2;
}

.attable{
    background-color: white;
}

.attable th {
    background-color: #463B3B;
    color: #fff;
    padding: 0.5em 1em;
}

.attable td {
    border: 1px solid black;
    padding: 0.5em 1em;
}

.attable input {
    cursor: pointer;
}

/* Column types */
.attable th.missed-col {
    background-color: #463B3B;
}

.attable td.missed-col {
    background-color: #ffecec;
    color: #f58aa6;
    text-align: center;
}

